<template>
<commonCharts :configs="configs"></commonCharts>
</template>

<script>
import echarts from 'echarts';
import commonCharts from '../widget/commonCharts';
export default {
  name: 'app-xmfxlx',
  data(){
    return{
      configs:{
        showDMW:false,////
        topTitles:['','','','','',''],//
        daLists:[{title:'',data:[6,6,6,6,6]},//title
        {title:'',data:[6,6,6,6,6]},
        {title:'',data:[6,6,6,6,6]},
        {title:'',data:[6,6,6,6,6]},
        {title:'',data:[6,6,6,6,6]},
        {title:'',data:[6,6,6,6,6]}]  
      }
    }
  },
  components:{commonCharts},
  mounted:function(){
    var fxlxEharts = echarts.init(document.querySelector('#echarts'));
    var option = {
        tooltip: {
            trigger: 'axis'
        },
        backgroundColor: 'rgba(255,245,245, 0.73)',

          xAxis:  {
              type: 'category',
              boundaryGap: false,
              data: ['','','','','','','']
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value} C'
              },
              splitLine:{
                  show:false
              },
              axisTick:{
                show:false
              }
          },
          series: [
              {
                  name:'',
                  type:'line',
                  data:[11, 11, 15, 13, 12, 13, 10]
              },
              {
                  name:'',
                  type:'line',
                  data:[1, 6, 2, 5, 3, 2, 0]
              }
          ]
        };
    fxlxEharts.setOption(option)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
