import main from '@/components/app/main'
import zl from '@/components/app/zl'
import xmjd from '@/components/app/xmjd'
import xmzt from '@/components/app/xmzt'
import xmlx from '@/components/app/xmlx'
import lxxq from '@/components/app/lxxq'
import xmfxlx from '@/components/app/xmfxlx'
import gzl from '@/components/app/gzl'
import gzlxq from '@/components/app/gzlxq'
import xmsx from '@/components/app/xmsx'
import lxxx from '@/components/app/lxxx'
import bmsx from '@/components/app/bmsx'
import sxjg from '@/components/app/sxjg'
import xmxq from '@/components/app/xmxq'
import xmcy from '@/components/app/xmcy'

const routes =  [
  {
    path: '/',
    name: 'main',
    component: main,
    meta:{
      title:''
    },
  },
  {
    path: '/zl',
    name: 'zl',
    component: zl,
    meta:{
      title:''
    }
  },
  {
    path: '/xmjd',
    name: 'xmjd',
    component: xmjd,
    meta:{
      title:''
    }
  },
  {
    path: '/xmzt',
    name: 'xmzt',
    component: xmzt,
    meta:{
      title:''
    }
  },
  {
    path: '/xmlx',
    name: 'xmlx',
    component: xmlx,
    meta:{
      title:''
    }
  },
  {
    path: '/xmlx/detail/:id',
    name: 'lxxq',
    component: lxxq,
    meta:{
      title:'' //idtitle
    },
    beforeEnter:function(to,from,next){
      if(to.params.id == 'xmfxlx'){
        to.meta.title = ''
      }else{
        to.meta.title = ''
      }
      next();
    }
  },
  {
    path: '/gzl',
    name: 'gzl',
    component: gzl,
    meta:{
      title:''
    }
  },
  {
    path: '/gzl/detail/:id',
    name: 'gzlxq',
    component: gzlxq,
    meta:{
      title:''  //idtitle
    },
    beforeEnter:function(to,from,next){
      if(to.params.id == 1){
        to.meta.title = ''
      }else if(to.params.id == 2){
        to.meta.title = ''
      }else{
        to.meta.title = ''
      }
      next();
    }
  },
  {
    path: '/xmsx',
    name: 'xmsx',
    component: xmsx,
    meta:{
      title:'/'
    }
  },
  {
    path: '/xmsx/option/:id',
    name: 'lxxx',
    component: lxxx,
    meta:{
      title:''
    },
    beforeEnter:function(to,from,next){
      if(to.params.id == 1){
        to.meta.title = ''
      }else if(to.params.id == 2){
        to.meta.title = ''
      }else if(to.params.id == 3){
        to.meta.title = ''
      }else if(to.params.id == 4){
        to.meta.title = ''
      }else{
        to.meta.title = ''
      }
      next();
    }
  },
  {
    path: '/xmsx/option/:id/:cid',
    name: 'bmsx',
    component: bmsx,
    meta:{
      title:''
    }
  },
  {
    path: '/xmsx/sxjg',
    name: 'sxjg',
    component: sxjg,
    meta:{
      title:''
    }
  },
  {
    path: '/xmsx/xmxq',
    name: 'xmxq',
    component: xmxq,
    meta:{
      title:''
    },
  },
  {
    path: '/xmsx/xmxq/xmcy',
    name: 'xmcy',
    component: xmcy,
    meta:{
      title:''
    }
  }
]

export default routes



