<template>
  <div class="detail">
    <div class="detailMain">
      <div class="detailtitle clearfix">
        <i class="detailflag"></i>
        <h4 class="detailname">{{configs.detailname}}</h4>
      </div>
      <div class="detailcontainer">
        <ol>
          <li><span class="detailItems"></span><span class="detailexplainItems">{{configs.detailstarttime}}</span></li>
          <li><span class="detailItems"></span><span class="detailexplainItems">{{configs.detailendtime}}</span></li>
          <li><span class="detailItems"></span><span class="detailexplainItems">{{configs.detailphase}}</span></li>
          <li class="clearfix"><span class="detailItems fl"></span><progress class="detailprogressItems fl" :value="configs.detailprogress" max="100"></progress><span class="detailprogressCount">{{configs.detailprogress}}%</span></li>
          <li><span class="detailItems"></span><span class="detailexplainItems">{{configs.detailstate}}</span></li>
          <li><span class="detailItems">()</span><span class="detailexplainItems">{{configs.detailworkload}}</span></li>
          <li class="clearfix">
            <span class="detailItems fl"></span>
            <router-link class="detailmemberItems fl" to="/xmsx/xmxq/xmcy">{{configs.detailmembercount}}</router-link>
          </li>
          <li class="clearfix">
            <span class="detailItems"></span>
          </li>
          <li class="detailwrapper clearfix">
            <div class="detaildrawing" id="echarts"></div>
          </li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  import {mapState} from 'vuex';

  export default {
    name: 'app-xmxq',
    data () {
      return {}
    },
    computed:{
      ...mapState({
        configs:state=>state.xmxqModule.xmxqConfigs
      })
    },
    created(){
      this.$store.dispatch('getXmxqPageData')
      this.$store.dispatch('getXmxqEchartsData', 'day')
    },
    mounted(){
    }
  }
</script>

<style lang="scss">
@import "../../assets/sass/detail.scss";
</style>



